
<template>
  <div class="common-layout">
    <el-container>
      <el-aside width="500px">
        <div id="app" style="width: 480px;text-align: center;">
          <h1>My To-Do List</h1>
          <to-do-form @todo-added="addToDo"></to-do-form>
          <h2 id="list-summary" ref="listSummary" tabindex="-1">{{listSummary}}</h2>
          <ul aria-labelledby="list-summary" class="stack-large">
            <li v-for="item in ToDoItems" :key="item.id">
              <to-do-item
                :label="item.label"
                :done="item.done"
                :id="item.id"
                @checkbox-changed="updateDoneStatus(item.id)"
                @item-deleted="deleteToDo(item.id)"
                @item-edited="editToDo(item.id, $event)">
              </to-do-item>
            </li>
          </ul>
        </div>
      </el-aside>
      <el-container>
        
        <el-header>代办事项</el-header>
        <el-main style="background-color: #f0f0f0;">
          <el-calendar @select="handleDateSelect">
            <template #date-cell="{ data }">
              <p :class="data.isSelected ? 'is-selected' : ''" @click="handleDateSelect(data)">
                {{ data.day.split('-').slice(1).join('-') }}
                {{ data.isSelected ? '✔️' : '' }}
              </p>
            </template>
          </el-calendar>
          <el-table
            :data="tableData"
            style="width: 100%;margin-top: 30px;text-align: center;"
            :row-class-name="tableRowClassName"
          >
            <el-table-column prop="date" label="时间" width="180" />
            <el-table-column prop="name" label="事项" width="180" />
            <el-table-column prop="status" label="是否完成" />
          </el-table>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import ToDoItem from "./components/ToDoItem.vue";
import uniqueId from "lodash.uniqueid";
import ToDoForm from "./components/ToDoForm.vue";

export default {
  name: "app",
  components: {
    ToDoItem,ToDoForm,
  },
  data() {
    return {
      ToDoItems: [
        { id: uniqueId("todo-"), label: "Learn Vue", done: false },
        {
          id: uniqueId("todo-"),
          label: "Create a Vue project with the CLI",
          done: true,
        },
        { id: uniqueId("todo-"), label: "Have fun", done: true },
        { id: uniqueId("todo-"), label: "Create a to-do list", done: false },
      ],
      selectedDate: null,
      tableData: [],
    };
  },

  methods: {
    addToDo(toDoLabel) {
      this.ToDoItems.push({
        id: uniqueId("todo-"),
        label: toDoLabel,
        done: false,
      });
    },
    updateDoneStatus(toDoId) {
      const toDoToUpdate = this.ToDoItems.find((item) => item.id === toDoId)
      toDoToUpdate.done = !toDoToUpdate.done
    },
    deleteToDo(toDoId) {
      const itemIndex = this.ToDoItems.findIndex((item) => item.id === toDoId);
      this.ToDoItems.splice(itemIndex, 1);
      this.$refs.listSummary.focus();
    },
    editToDo(toDoId, newLabel) {
      const toDoToEdit = this.ToDoItems.find((item) => item.id === toDoId);
      toDoToEdit.label = newLabel;
    },
    handleDateSelect(data) {
      this.selectedDate = data.day;
      this.updateTableData();
    },
    updateTableData() {
      this.tableData = this.ToDoItems.map(item => ({
        date: this.selectedDate,
        name: item.label,
        status: item.done ? '完成' : '未完成'
      }));
    }

  },
  computed: {
  listSummary() {
    const numberFinishedItems = this.ToDoItems.filter((item) =>item.done).length
    return `${numberFinishedItems} out of ${this.ToDoItems.length} items completed`
  },
  

},

};

</script>

